<template>
  <div>
    <TopScenario kind="" :name="name"></TopScenario>
    <div class="top-btn">
      <div>
        <button>
          <router-link to="/evolution"></router-link>
        </button>
        <button></button>
      </div>
      <div>
        <button></button>
        <button>
          <router-link to="/project/home"></router-link>
        </button>
      </div>
    </div>
    <div class="box1">
      <div class="ylevel">
        <label for="name">:</label>
        <input class="xlevel" id="name">
      </div>
      <div class="ylevel">
        <label for="discription">:</label>
        <input class="xlevel" id="discription">
      </div>
      <div class="ylevel">
        <label for="discription">:</label>
        <input class="xlevel" id="precondition">
      </div>
      <div class="ylevel">
        <label for="primaryRole">:</label>
        <input class="xlevel" id="primaryRole">
      </div>
      <div class="ylevel">
        <label for="secondaryRole">:</label>
        <input class="xlevel" id="secondaryRole">
      </div>
      <div class="ylevel">
        <label for="dependency">:</label>
        <input class="xlevel" id="dependency">
      </div>
      <div class="ylevel">
        <label for="dependency">:</label>
        <input class="xlevel" id="input">
      </div>
      <div class="ylevel">
        <label for="dependency">:</label>
        <input class="xlevel" id="output">
      </div>
      <div class="ylevel">
        <label for="data">:</label>
        <input class="xlevel" id="data">
      </div>
      <div class="flow businesss-flow">
        <SpecialTable ref="bflow"  @otherData="otherData" title="" tag="Step" :data="basicFlow"></SpecialTable>
      </div>
    </div>
    <div class="box2">
      <div class="up"><span></span></div>
      <div class="box2-body">
          <div class="col-name">
            <span class="focus-color"></span>
          </div>
          <div class="col-operate">
            <span  class="focus-color" @click="del(index)"></span>
          </div>
      </div>
      <p>
        <router-link to='/project/scenario/activity'>
          <Button type="primary"></Button>
        </router-link>
      </p>
    </div>
    <div class="box3">
      <div class="up"><span></span></div>
      <p>
        <button></button>
      </p>
    </div>
  </div>
</template>
<style scoped>
  button {
    margin-right: 20px;
    padding: 5px;
    color: #fff;
    background-color: blue;
    outline: none;
  }
  p {
    height: 30px;
  }
  .top-btn{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    border-bottom: 1px solid lightgray;
  }
  .focus-color {
    cursor: pointer;
    color: blue;
  }
  .box1 {
    position: relative;
    margin: 20px 0;
    text-align: left;
  }
  .ylevel {
    padding: 10px;
  }
  .ylevel label {
    color: gray;
  }
  .xlevel {
    position: absolute;
    left: 100px;
    width: 500px;
  }
  .box2, .box3 {
    padding: 30px 0;
    text-align: left;
    border-bottom: 1px solid lightgray;
  }
  .box2-body {
    padding-top: 5px;
  }
  .col-name {
    width: 40%;
  }
  .col-operate {
    width: 15%;
  }
  .up {
    height: 40px;
    font-size: 16px;
    font-weight: bold;
  }
</style>
<script>
  /**type
   * 
   * */

  import TopScenario from '@/components/TopScenario'
  import {Button} from 'iview'
  export default{
    data () {
      return {
        type: '',
        name: '',
        infos: [
          {
            role: '',
            system: ''
          }
        ]
      }
    },
    components: {
      TopScenario,
      Button
    },
    beforeMount: function () {
      console.log(this.type)
      this.type = this.$route.params.type
      this.name = this.type === 'new' ? '' : ''
    },
    methods: {
      del: function (index) {
        // do something
        this.infos.splice(index, 1)
      },
      add: function () {
        this.infos.push({})
      }
    }
  }
</script>



